import React, {useEffect} from 'react'
import {Link} from 'react-router-dom'
import { Button, Row ,Col , ListGroup, Image, Card} from 'react-bootstrap'
import {useDispatch, useSelector} from 'react-redux'
import Message from '../components/Message.js'
//import {getUserDetails, updateUserProfile} from '../actions/userActions.js'
import {createOrder} from '../actions/orderActions.js'
import CheckoutSteps from '../components/CheckoutSteps.js'



const PlaceOrderScreen =  ({history}) => {
   const dispatch = useDispatch()
  const cart = useSelector(state => state.cart)

    //CALCULATING THE PRICES
  const addDecimals = (num) => { return(Math.round(num*100)/100).toFixed(2) }

    cart.itemsPrice = addDecimals(cart.cartItems.reduce((acc, item)=>acc +item.price*item.qty,0))

    cart.shippingPrice = addDecimals(cart.itemsPrice > 100 ? 0 : 10)

    cart.taxPrice = addDecimals(Number((0.10*cart.itemsPrice).toFixed(2)))

    cart.totalPrice = (Number(cart.itemsPrice) + Number(cart.shippingPrice)+ Number(cart.taxPrice)).toFixed(2)
   //if your total price is looking funny, just unwrap addDecimals and wrap in .toFixed(2)

  const orderCreate = useSelector(state => state.orderCreate )
  const {order,success,error} = orderCreate
    console.log(order)
  useEffect(()=>{  
    if(success){
    history.push(`/order/${order._id}`)
    //eslint-disable-next-line
    }
  },[history,success/*,order._id*/])



  const placeOrderHandler = () => {dispatch(createOrder({
     orderItems:cart.cartItems,
     shippingAddress:cart.shippingAddress,
     paymentMethod:cart.paymentMethod,
     itemsPrice:cart.itemsPrice,
     shippingPrice:cart.shippingPrice,
     taxPrice:cart.taxPrice,
     totalPrice:cart.totalPrice

  }))}

        return(
         <>
        <CheckoutSteps step1 step2 step3 step4/>
        <Row>
         <Col md={8}>

          <ListGroup variant="flush">
           <ListGroup.Item>
             <h2>Shipping</h2>
             <p>
             <strong>Address:</strong>
             {cart.shippingAddress.address},{cart.shippingAddress.city}{' '},
             {cart.shippingAddress.postalCode}{' '},{cart.shippingAddress.country}
             </p>
            </ListGroup.Item>

              <ListGroup.Item>
               <h2>Payment Method</h2>
                <strong>Method:</strong>
                {cart.paymentMethod}
             </ListGroup.Item>

             <ListGroup.Item>
              <h2>Order Items</h2>

               {cart.cartItems.length === 0 ? <Message>Your cart is empty </Message>:(
                <ListGroup variant="flush">
                  {cart.cartItems.map((item, index) =>(

                    <ListGroup.Item key ={index}>
                    <Row>
                     <Col md={1}>
                      <Image src={item.image} alt={item.name} fluid rounded/>
                      <Link to={`product/${item.product}`/*remeber product property is the id in the cart*/}>
                       {item.name}
                      </Link>
                      </Col>
                       <Col md={4}>
                       {item.qty} x ${item.price} = {item.qty*item.price}
                       </Col>

                      </Row>

                    </ListGroup.Item>

                  ))}
                </ListGroup>
               ) }
            </ListGroup.Item>
          </ListGroup>
         </Col>

         <Col md={4}>
           <Card>
              <ListGroup>
               <ListGroup.Item>
                <h2>Order Summary</h2>
               </ListGroup.Item>

               <ListGroup.Item>
                <Row>

                 <Col>Items </Col>
                 <Col>${cart.itemsPrice} </Col>

                </Row>
               </ListGroup.Item>

               <ListGroup.Item>
                <Row>

                 <Col>Shipping </Col>
                 <Col>${cart.shippingPrice} </Col>

                </Row>
               </ListGroup.Item>

               <ListGroup.Item>
                <Row>

                 <Col>Tax </Col>
                 <Col>${cart.taxPrice} </Col>

                </Row>
               </ListGroup.Item>

               <ListGroup.Item>
                <Row>

                 <Col>Total </Col>
                 <Col>${cart.totalPrice} </Col>

                </Row>
               </ListGroup.Item>
                {/*place error here*/}
                <ListGroup.Item>
                 {error&&<Message variant='danger'>{error} </Message>}
                </ListGroup.Item>

               <ListGroup.Item> <Button type='button' className='btn-block' disabled={cart.cartItems ===0} onClick={placeOrderHandler}>
               Place Order
               </Button>
               </ListGroup.Item>

             </ListGroup>
           </Card>

         </Col>
        </Row>
         </>

        )
}


export default PlaceOrderScreen;
